import React, { Component } from 'react';
import JsonEditor from './JsonEditor';
import JsonViewer from './JsonViewer';
import Toaster from './Toaster';

export default class JsonFormatter extends Component {
  sample = {
    "name": "Gopinath Nelluri",
    "email": "hello@gopinath.us",
    "location": {
      "city": "Tampa",
      "state": "Florida"
    },
  }
  state = {
    stringData: JSON.stringify(this.sample, null, "\t"),
    jsonData: { "name": "Gopinath Nelluri", "email": "hello@gopinath.us", "location": { "city": "Tampa", "state": "Florida" }},
    editorMode: "text",
    toastMessages: []
  }

  deleteToastMessage = (id) => {
    this.setState((prevState) => {
      let toastMessages = prevState.toastMessages;
      toastMessages.splice(id, 1);
      return {
        toastMessages: toastMessages
      }
    });
  }

  addToastMessage = (data) => {

    this.setState((prevState) => {
      let toastMessages = [...prevState.toastMessages];
      toastMessages.push(data)
      return {
        toastMessages: toastMessages
      }
    });
  }
  onChange = (data) => {
    try {
      //if (data.length < 1000){
        let tempJSON = JSON.parse(data);
        this.setState(() => {
            return {
              stringData: data,
              jsonData: tempJSON
            }
          })
      //}
    } catch (e) {
    }
  }

  updateJsonData = (data) => {
    this.setState(() => {
      return {
        stringData: JSON.stringify(data.updated_src, null, "\t"),
        jsonData: data.updated_src
      }
    })
  }

  importJsonData = (data) => {
    this.setState(() => {
      return {
        stringData: JSON.stringify(data, null, "\t"),
        jsonData: data
      }
    })
  }

  butifyJSONData = () => {
    this.setState((prevState) => {
      return {
        stringData: JSON.stringify(prevState.jsonData, null, "\t")
      }
    })
  }

  minifyJSONData = () => {
    this.setState((prevState) => {
      return {
        stringData: JSON.stringify(prevState.jsonData)
      }
    })
  }

  changeEditorMode = (mode) => {
    this.setState(() => {
      return {
        editorMode: mode
      }
    })
  }

  render() {
    return (
      <div className="jsonformatter">
        <div className="container-fluid">
          <div className="row">
            <div className="col-sm">
              <JsonEditor onChange={this.onChange}
                stringData={this.state.stringData}
                jsonData={this.state.jsonData}
                editorMode={this.state.editorMode}
                updateJsonData={this.updateJsonData}
                changeEditorMode={this.changeEditorMode}
                butifyJSONData={this.butifyJSONData}
                minifyJSONData={this.minifyJSONData}
                addToastMessage={this.addToastMessage}
                importJsonData={this.importJsonData}>
              </JsonEditor>
            </div>
            <div className="col-sm">
              <JsonViewer addToastMessage={this.addToastMessage} updateJsonData={this.updateJsonData} jsonData={this.state.jsonData}></JsonViewer>
            </div>
          </div>
        </div>
        <Toaster deleteToastMessage={this.deleteToastMessage} messages={this.state.toastMessages} ></Toaster>
      </div>
    )
  }


}
