import React, { Component } from 'react';
import ToastMessage from './ToastMessage';

export default class Toaster extends Component {

  render() {
    return (
      <div className="toaster">
        {
          this.props.messages.length > 0 && [...this.props.messages].reverse().map((item, index) => {
              return <ToastMessage key={this.props.messages.length - 1 - index} deleteToastMessage={this.props.deleteToastMessage} id={this.props.messages.length - 1 - index} data={item}></ToastMessage>
          })
        }
      </div>
    )
  }
}
