import React, { Component } from 'react';
import ReactJson from 'react-json-view'
import {ObjectInspector} from 'react-inspector';
import Inspector from 'react-json-inspector';
import './../styles/components/JsonViewer.scss';
import ButtonToolbar from 'react-bootstrap/ButtonToolbar';
import ButtonGroup from 'react-bootstrap/ButtonGroup';
import Button from 'react-bootstrap/Button';

import Fullscreen from "react-full-screen";
import fullscreenmode from './../assets/images/full-screen.png';
import normalscreenmode from './../assets/images/normal-screen.png';

import * as FileSaver from 'file-saver';

export default class JsonViewer extends Component {
  state = {
    "viewerMode": "1",
    "isFull": false
  }
  copyToClipboard = (data) => {
    this.props.addToastMessage({
      timestamp: Date.now(),
      message: "JSON data successfully copied!"
    });
  }

  useModeOne = () => {
    this.setState(()=>{
      return {
        "viewerMode": "1"
      }
    })
  }

  useModeTwo = () => {
    this.setState(()=>{
      return {
        "viewerMode": "2"
      }
    })
  }

  useModeThree = () => {
    this.setState(()=>{
      return {
        "viewerMode": "3"
      }
    })
  }

  editFullScreenMode = () => {
    this.setState((prevState) => {
      return {
        isFull: !prevState.isFull
      }
    });
  }

  exportJSONdata = () => {
    let blob = new Blob([JSON.stringify(this.props.jsonData, null, "\t")], {type : "application/json"});
    FileSaver.saveAs(blob,"data.json");
  }

  render() {
    return (
      <div className="jsonviewer">
        <nav className="inner-nav-bar navbar navbar-dark bg-dark">
          <h2 className="navbar-brand fix-navheader">Viewer</h2>
          <ButtonToolbar>
            <ButtonGroup className="mr-2 pull-left" aria-label="First group">
              <Button onClick={this.exportJSONdata}  variant="secondary mr-1">Export</Button>
            </ButtonGroup>
            <ButtonGroup className="mr-2" aria-label="Third group">
              <Button onClick={this.useModeOne} active={this.state.viewerMode === "1"} variant="outline-light mr-1">1</Button>
              <Button onClick={this.useModeTwo} active={this.state.viewerMode === "2"} variant="outline-light mr-1">2</Button>
              <Button onClick={this.useModeThree} active={this.state.viewerMode === "3"} variant="outline-light">3</Button>
            </ButtonGroup>
          </ButtonToolbar>
        </nav>
        <Fullscreen
          enabled={this.state.isFull}
          onChange={isFull => this.setState({ isFull })}
        >
        <div className={this.state.isFull?'in-fullscreen-mode jsonviewer-body':'jsonviewer-body'}>
          {
          this.state.viewerMode === "2" && <ObjectInspector data={this.props.jsonData} />
          }
          {this.state.viewerMode === "1" && <ReactJson
            name={false}
            src={this.props.jsonData}
            enableClipboard={this.copyToClipboard}
            iconStyle="square"
            theme="bright:inverted"
            groupArraysAfterLength={50}
            collapsed={1}
            collapseStringsAfterLength={200}
          />}
          {
          this.state.viewerMode === "3" && <Inspector data={this.props.jsonData} />
          }
          <div onClick={this.editFullScreenMode} className="screenmode">
            <img src={this.state.isFull?normalscreenmode:fullscreenmode} height="20" className="d-inline-block align-top" alt="JSON Formatter &amp; Validator" />
          </div>
        </div>
        </Fullscreen>
      </div>
    )
  }
}
