import React, { Component } from 'react';
import Toast from 'react-bootstrap/Toast'
import logo from '../logo.png';

export default class ToastMessage extends Component {
  state = {
    show: true
  }
  hideToastMessage = () => {
    this.setState(()=>{
      return {
        show: false
      }
    })
    this.props.deleteToastMessage(this.props.id);
  }

  render() {
    return (
      <div className="toastmessage">
        <Toast onClose={this.hideToastMessage} show={this.state.show} delay={3000} autohide>
          <Toast.Header>
            <img
              src={logo} height="20"
              className="rounded mr-2"
              alt=""
            />
            <small className="flex-fill"></small>
          </Toast.Header>
          <Toast.Body>
            <span className={this.props.data.className}>{this.props.data.message}</span>
          </Toast.Body>
        </Toast>
      </div>
    )
  }
}
