import React, { Component } from 'react';
import AceEditor from 'react-ace';
import 'brace/mode/json';
import 'brace/theme/github';
import ButtonToolbar from 'react-bootstrap/ButtonToolbar';
import ButtonGroup from 'react-bootstrap/ButtonGroup';
import Button from 'react-bootstrap/Button';
import ReactJson from 'react-json-view'
import Fullscreen from "react-full-screen";
import fullscreenmode from './../assets/images/full-screen.png';
import normalscreenmode from './../assets/images/normal-screen.png';

export default class JsonEditor extends Component {
  fileReader;

  state = {
    "isFull": false
  }

  useTextMode = () => {
    this.props.changeEditorMode("text");
  }

  useAdvancedMode = () => {
    this.props.changeEditorMode("advanced");
  }

  inputFile = (event) => {
    if (event.target.files[0]) {
      this.fileReader = new FileReader();
      this.fileReader.onloadend = this.handleFileRead
      this.fileReader.readAsText(event.target.files[0]);
    }
  }

  handleFileRead = () => {
    const content = this.fileReader.result;
    try {
      let jsonData = JSON.parse(content);
      this.props.importJsonData(jsonData);
      this.props.addToastMessage({
        timestamp: Date.now(),
        message: "File imported successfully!"
      });
    } catch (e) {
      console.log(e.message)
      this.props.addToastMessage({
        timestamp: Date.now(),
        message: e.message,
        className: "error-message"
      });
    }
  }

  editFullScreenMode = () => {
    this.setState((prevState) => {
      return {
        isFull: !prevState.isFull
      }
    });
  }

  render() {
    return (
      <div className="jsoneditor">
        <nav className="inner-nav-bar navbar navbar-dark bg-dark">
          <h2 className="navbar-brand fix-navheader">Editor</h2>
          <ButtonToolbar>
            <ButtonGroup className="mr-2 pull-left" aria-label="First group">
              <label className="secondary btn btn-secondary mr-1 margin-bottom-fix">Import
                <input onChange={this.inputFile} className="hide-element" type="file" name="inputFile" />
              </label>
            </ButtonGroup>
            <ButtonGroup className="mr-2" aria-label="Second group">
              {this.props.editorMode === "text" && <Button onClick={this.props.minifyJSONData} variant="secondary mr-1">Minify</Button>}
              {this.props.editorMode === "text" && <Button onClick={this.props.butifyJSONData} variant="secondary mr-1">Beautify</Button>}
            </ButtonGroup>
            <ButtonGroup className="mr-2" aria-label="Third group">
              <Button onClick={this.useTextMode} active={this.props.editorMode === "text"} variant="outline-light mr-1">Text</Button>
              <Button onClick={this.useAdvancedMode} active={this.props.editorMode !== "text"} variant="outline-light">Advanced</Button>
            </ButtonGroup>
          </ButtonToolbar>
        </nav>
        <Fullscreen
          enabled={this.state.isFull}
          onChange={isFull => this.setState({ isFull })}
        >
          <div className={this.state.isFull?'in-fullscreen-mode jsoneditor-body':'jsoneditor-body'}>
          {this.props.editorMode === "text" && <AceEditor
            width="auto"
            height="100%"
            mode="json"
            theme="github"
            onChange={this.props.onChange}
            value={this.props.stringData}
            name="JSON_EDITOR"
            editorProps={{ $blockScrolling: true }}
            wrapEnabled={true}
          />}
          {
            this.props.editorMode !== "text" && <div className="jsoneditor-body-advanced">
              <ReactJson
                name={false}
                src={this.props.jsonData}
                onAdd={this.props.updateJsonData}
                onDelete={this.props.updateJsonData}
                onEdit={this.props.updateJsonData}
                iconStyle="square"
                theme="bright:inverted"
                groupArraysAfterLength={50}
                collapsed={1}
                collapseStringsAfterLength={200}
              />
            </div>
          }
          <div onClick={this.editFullScreenMode} className="screenmode">
            <img src={this.state.isFull?normalscreenmode:fullscreenmode} height="20" className="d-inline-block align-top" alt="JSON Formatter &amp; Validator" />
          </div>
        </div>
        </Fullscreen>
      </div>
    )
  }
}

